import {Components} from "../Images";
import React from "react";
import styled from "styled-components";

const TextStyle = styled.div`
  font-family: Inter;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px; 
  color: ${props => props.theme.color.black0};
  
  .tablet & {
    font-size: 15px;
    line-height: 18px;
  }
`;

const AboutUsList = [
    {
        logo: Components.aboutUs[0],
        description: <TextStyle>  <br />  - 7 </TextStyle>
    },
    {
        logo: Components.aboutUs[1],
        description: <TextStyle>20+    </TextStyle>,
    },
    {
        logo: Components.aboutUs[2],
        description: <TextStyle>100+   </TextStyle>,
    },
    {
        logo: Components.aboutUs[3],
        description: <TextStyle>   </TextStyle>
    },
];

export default AboutUsList;